<template>
  <div class="nav-header">
    <el-menu :default-active="getSectionPath()" mode="horizontal" background-color="#323232" text-color="#8a8a8a" active-text-color="#65afd9">
      <el-menu-item index="/">
        <img style="width: 200px; margin-right: 20px" alt="kookle-logo" src="@/assets/images/logo-01.svg">
      </el-menu-item>
      <el-menu-item index="/oracle" @click="onMenuClick('oracle')">Oracle</el-menu-item>
      <el-menu-item index="/wallet" @click="onMenuClick('wallet')">Private & Seed Wallet</el-menu-item>
      <el-submenu index="/demo">
        <template slot="title">Demo</template>
        <el-menu-item index="/demo/thai-lottery" @click="onMenuClick('thaiLottery')">Thai Lottery</el-menu-item>
      </el-submenu>
      <div class="connect-wallet-button">
        <el-button style="border: transparent !important; font-size: 16px; padding: 0; color: rgb(110 213 213) !important; margin-right: 10px;">
          <div style="display: flex">
            <div style="border-radius: 50%; margin-left: 5px; margin-top: 2px;">
              <img alt="kookl-coin" style="width: 32px" src="@/assets/images/kookl-coin.png">
            </div>
            <div style="padding: 11px 15px 10px 10px; font-size: 14px">$178.45</div>
          </div>
        </el-button>
        <div v-if="isCorrectNetWork" class="network-label polygon">
          <i class="el-icon-success"></i> Polygon
        </div>
        <div v-else class="network-label wrong">
          <i class="el-icon-warning"></i>Wrong Network
        </div>
        <el-button v-if="!isWalletConnected" style="font-size: 14px; padding: 13px; border: transparent !important;" type="default" @click="connectWallet">
          Connect Wallet
        </el-button>
        <el-button v-else style="font-size: 14px; padding: 13px; border: transparent !important;" type="default" @click="dialogVisible = true">
          <span style="letter-spacing: 1px">{{getShortWalletAddress()}}</span>
        </el-button>
      </div>
    </el-menu>
    <el-dialog
      title="YOUR WALLET"
      :visible.sync="dialogVisible"
      width="30%">
      <div>
        <input type="hidden" id="contract-address" :value="currentAddress" ref="contractAddressCopy">
        <div class="contract-address">{{currentAddress}}</div>
        <div style="display: flex; align-items: baseline; margin-top: 15px;">
          <div style="margin-right: 20px">
            <a :href="`${bscScanUrl}/address/${currentAddress}`" target="_blank" style="color: var(--primary-color); text-decoration: none">
              View on BscScan <img style="width: 15px; margin-left: 5px" alt="kookl-coin" src="@/assets/images/icon/foreign.png">
            </a>
          </div>
          <div>
            <copy-to-clipboard :text="currentAddress" @copy="copyWalletAddress">
              <span style="color: var(--primary-color); text-decoration: none; cursor: pointer">
                Copy Address <i class="el-icon-copy-document" style="font-size: 19px"></i>
              </span>
            </copy-to-clipboard>
          </div>
        </div>
      </div>
      <div slot="footer" class="dialog-footer" style="text-align: center">
        <el-button @click="disconnectWallet" style="border: 2px solid !important; font-weight: bold">LOGOUT</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import i18n from '@/i18n'
import Web3 from 'web3'
import { VUEX_EVENT } from '@/configs/constant'
import CopyToClipboard from 'vue-copy-to-clipboard'
import LocalStorageFactory from '@/factories/local-storage-factory'

const bscScanUrl = process.env.VUE_APP_BSC_SCAN_URL
const bscNetworkId = process.env.VUE_APP_BSC_NETWORK_ID

export default {
  name: 'HeaderTemplate',
  components: { CopyToClipboard },
  data () {
    return {
      bscScanUrl,
      dialogVisible: false,
      isWalletConnected: false,
      isCorrectNetWork: false,
      accountAddressList: [],
      currentAddress: ''
    }
  },
  created: async function () {
    this.isWalletConnected = LocalStorageFactory.getIsWalletConnected()

    if (this.isWalletConnected === null || this.isWalletConnected) {
      await this.connectWallet()
    }
  },
  methods: {
    changeLang: function (lang) {
      i18n.locale = lang
    },
    connectWallet: async function () {
      this.isWalletConnected = await this.ethEnabled()

      this.accountAddressList = await window.web3.eth.getAccounts()
      this.currentAddress = this.accountAddressList[0]

      this.$store.dispatch(VUEX_EVENT.WALLET.SET_WALLET_ADDRESS, this.currentAddress)
      this.$store.dispatch(VUEX_EVENT.WALLET.SET_IS_WALLET_CONNECT, this.isWalletConnected)

      LocalStorageFactory.setIsWalletConnected(Number(this.isWalletConnected))

      const network = await window.web3.eth.getChainId()
      this.isCorrectNetWork = (network === Number(bscNetworkId))
    },
    ethEnabled: async function () {
      if (window.ethereum) {
        window.web3 = new Web3(window.ethereum)
        await window.ethereum.enable()

        return true
      }

      return false
    },
    getShortWalletAddress: function () {
      return `${this.currentAddress.substring(0, 6)}...${this.currentAddress.substring(this.currentAddress.length - 4, this.currentAddress.length)}`
    },
    getSectionPath: function () {
      let mainPath = this.$route.path.split('/')[1]
      let subPath = this.$route.path.split('/')[2]

      const menuIndex = (subPath !== '' && subPath !== undefined) ? `/${mainPath}/${subPath}` : `/${mainPath}`

      return menuIndex
    },
    copyWalletAddress: function () {
      this.$message({
        message: 'Copied address to clipboard.',
        type: 'success'
      })
    },
    disconnectWallet: async function () {
      this.isWalletConnected = false
      this.$store.dispatch(VUEX_EVENT.WALLET.SET_IS_WALLET_CONNECT, this.isWalletConnected)

      LocalStorageFactory.setIsWalletConnected(Number(this.isWalletConnected))

      this.dialogVisible = false
    },
    onMenuClick: function (menuName) {
      let routerName = ''

      switch (menuName) {
        case 'oracle':
          routerName = 'OracleList'
          break
        case 'wallet':
          routerName = 'LockWallet'
          break
        case 'thaiLottery':
          routerName = 'ThaiLottery'
          break
      }

      this.$router.push({
        name: routerName
      })
    }
  }
}
</script>
