  nav {
    padding: 0 20px;
  }

  .menu-list > div {
    cursor: pointer;
    padding: 30px;
    color: var(--primary-color-dark);
  }

  .menu-list > div:hover {
    color: #8a8a8a;
  }

  .connect-wallet-button {
    display: flex;
    margin: 23px 20px 23px 0;
    justify-content: flex-end;
  }

  .contract-address {
    font-weight: bold;
    font-size: 20px;
    /*color: var(--primary-color-dark);*/
    color: #cdcdcd;
  }

  .network-label {
    border-radius: 25px;
    padding: 12px 15px;
    font-size: 14px;
    margin-right: 10px;
    cursor: pointer;
  }

  .network-label:hover {
    opacity: 0.8;
  }

  .bsc {
    background-color: #261c00;
    color: #efb00f;
    font-weight: bold;
  }

  .polygon {
    background-color: #211d28;
    color: #b387ff;
    font-weight: bold;
  }

  .wrong {
    background-color: #280008;
    color: #ff0000;
  }

  .el-icon-warning {
    color: #ff0000;
    margin-right: 5px;
  }

  .network-label.bsc .el-icon-success {
    color: #efb00f;
    margin-right: 5px;
  }

  .network-label.polygon .el-icon-success {
    color: #b387ff;
    margin-right: 5px;
  }
</style>
