const KEY_IS_WALLET_CONNECTED = 'is-wallet-connected'

const setIsWalletConnected = (isWalletConnected) => {
  localStorage.setItem(KEY_IS_WALLET_CONNECTED, isWalletConnected)
}

const getIsWalletConnected = () => {
  const isWalletConnected = localStorage.getItem(KEY_IS_WALLET_CONNECTED)
  return (isWalletConnected !== null)? Boolean(parseInt(isWalletConnected)) : isWalletConnected
}

const clear = () => {
  localStorage.clear()
}

export default {
  setIsWalletConnected,
  getIsWalletConnected,
  clear
}
